.songs-wrap {
    :global {
        .songs-header {
            margin: 20px 0 10px;
            display: flex;
            justify-content: space-between;
        }

        .songs-list {
            .item {
                height: 30px;
                display: flex;
                font-size: 12px;
                align-items: center;

                &.even {
                    background: rgb(247, 247, 247);
                }

                .li-index {
                    width: 74px;
                    padding: 6px 10px;
                    line-height: 18px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    .icon-play {
                        width: 17px;
                        height: 17px;
                        cursor: pointer;
                        background: url(https://s2.music.126.net/style/web2/img/table.png?d3de0e45f5036e587ba7f96b210c7135) 0 -103px no-repeat;

                        &:hover {
                            background-position: 0 -128px;
                        }
                    }
                }

                .name {
                    padding: 6px 10px;
                    flex: 1;
                    display: flex;
                    align-items: center;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;
                    width: 20%;

                    .singer-name {
                        color: #333;
                        font-size: 12px;
                        text-decoration: none;
                        white-space: nowrap;

                        &:hover {
                            text-decoration: underline;
                        }
                    }

                    .mv-name {
                        color: #aeaeae;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        max-width: 250px;
                    }

                    .icon-mv {
                        display: inline-block;
                        width: 23px;
                        height: 17px;
                        margin: 1px 0 0 0;
                        background: url(https://s2.music.126.net/style/web2/img/table.png?d3de0e45f5036e587ba7f96b210c7135) 0 -151px no-repeat;

                        &:hover {
                            background-position: -30px -151px;
                        }

                    }
                }

                .option {
                    width: 100px;
                    position: relative;
                    height: 100%;
                    box-sizing: border-box;

                    .dt {
                        color: #666;
                        position: absolute;
                        top: 6px;
                        left: 10px;
                    }

                    .btns {
                        position: absolute;
                        top: 6px;
                        left: 10px;
                        display: none;

                        .icon {
                            display: inline-block;
                            cursor: pointer;
                            width: 18px;
                            height: 16px;
                            margin: 2px 0 0 4px;
                        }

                        .icon-add {
                            margin: 0 0 3px 0;
                            width: 13px;
                            height: 13px;
                            background: url(https://s2.music.126.net/style/web2/img/icon.png?68f683ea74b61d088af88c3a9aa4411b) 0 -700px no-repeat;

                            &:hover {
                                background-position: -22px -700px;
                            }
                        }

                        .icon-fav {
                            background: url(https://s2.music.126.net/style/web2/img/table.png?d3de0e45f5036e587ba7f96b210c7135) 0 -174px no-repeat;

                            &:hover {
                                background-position: -20px -174px;
                            }
                        }

                        .icon-share {
                            background: url(https://s2.music.126.net/style/web2/img/table.png?d3de0e45f5036e587ba7f96b210c7135) 0 -195px no-repeat;

                            &:hover {
                                background-position: -20px -195px;
                            }

                        }

                        .icon-down {
                            background: url(https://s2.music.126.net/style/web2/img/table.png?d3de0e45f5036e587ba7f96b210c7135) -81px -174px no-repeat;

                            &:hover {
                                background-position: -104px -174px;
                            }
                        }

                    }
                }

                &:hover {
                    .dt {
                        display: none;
                    }

                    .btns {
                        display: block;
                    }
                }

                .album {
                    width: 20%;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    color: #333;
                    text-decoration: none;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}